import React from "react";
import  Login from "./LoginClient";
import Register  from "./RegisterClient";

class FormClient extends React.Component {
  constructor(props) {
    super(props);
    this.handleLoggIN=this.handleLoggIN.bind(this);

    this.state = {
      isLogginActive: true
    };
  }

  componentDidMount() {
    //Add .right by default
    this.rightSide.classList.add("right");
  }

  handleLoggIN=()=>{
    this.props.LoggedIn()
  }
  
  changeState() {
    const { isLogginActive } = this.state;

    if (isLogginActive) {
      this.rightSide.classList.remove("right");
      this.rightSide.classList.add("left");
    } else {
      this.rightSide.classList.remove("left");
      this.rightSide.classList.add("right");
    }
    this.setState(prevState => ({ isLogginActive: !prevState.isLogginActive }));
  }

  render() {
    const { isLogginActive } = this.state;
    const current = isLogginActive ? "Inscription" : "Connexion";
    const currentActive = isLogginActive ? "Connexion" : "Inscription";
    
    return (
      <div >
        <div className="login">
          <div className="container" ref={ref => (this.container = ref)}>
            {isLogginActive && (
              <Login containerRef={ref => (this.current = ref)} SetLoggedIn={this.handleLoggIN} />
            )}
            {!isLogginActive && (
              <Register containerRef={ref => (this.current = ref)} />
            )}
          </div>
          <RightSide
            current={current}
            currentActive={currentActive}
            containerRef={ref => (this.rightSide = ref)}
            onClick={this.changeState.bind(this)}
          />
        </div>
      </div>
    );
  }
}

const RightSide = props => {
  return (
    <div
      className="right-side"
      ref={props.containerRef}
      onClick={props.onClick}
    >
      <div className="inner-container">
        <div className="text1">{props.current}</div>
      </div>
    </div>
  );
};

export default FormClient;